.card {
  z-index: 0;
  max-width: 40vw;
    text-align: left;
    margin: 15px;
    padding: 2%;
    border: 1px;
    background-color: inherit;
    display: inline-block;
    border-style: outset;
    border-radius: 5%;
    text-decoration: none;
  }
.card h2{
  text-align: left;
  margin: 0;
}
.card br{
  height: 0;
}
  .wrapper {
    display: grid;
    grid-template-columns: 0.9fr 0.1fr;
  }

.forked{
  margin-top: auto;

}
.modal-content{
  max-width: 80vw;
  background-color: #000000c2;
  border: 2px solid grey;
}

pre code{
  font-size: inherit;
  word-break:normal;
}
pre{
  color: inherit;
}