.mainWrapper{
    display: grid;
    grid-template-rows: 0.99fr 0.02fr 0.99fr;
}
.topside{
    display: grid;
    margin:auto;
    margin-left:2vw;
    margin-right:2vw;
}
.botside{
    display: grid;
    margin: auto;
    overflow-x: hidden;
}

.icon{
    fill: white;
    /* stroke: white; */
    transform: scale(1.5);
    margin: 1vw;
}
.icon:hover{
    transition: opacity 0.3s ease-in-out;
    opacity: 0.5;
}
a:focus{
    outline:0px;
    }
.li{
    text-align: center;
}