.hrgrid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: center;
    overflow-x:hidden;
}
.left{
display: grid;
margin: auto;
animation: Header-right 1 var(--show-time) ease-out;
}

.right{
    margin: auto;
    display: grid;
    animation: Header-left 1 var(--show-time) ease-out;
}

@media screen and (max-width:700px){
    .hrgrid {
        display: block;
        text-align: center;
    }
    .left{
        margin: auto;
        display: grid;
    }
    .right{
        display:grid;
    }
    .card{
        max-width: 90vw;
    }
  }