.CVinfo{
    display:inline;
}
.fadein{
    justify-content: center;
    text-align: center;
    display: grid;
    animation: Header-in 1 var(--show-time) ease-out;
}
@keyframes fade-in {
    from {
      transform: translateY(-100px);
    }
    to {
      transform: translateY(0px);
    }
  }