:root{
  --show-time: 2s;
  --background-color: white;
  --text-color: black
}
body{
  color: var(--background-color);
  margin: auto;
}
.App {
  text-align: center;
  text-decoration: none;
}
::-moz-selection { /* Code for Firefox */
  color: white;
  background: gray;
}

::selection {
  color: white;
  background: grey;
}

.App-logo {
  margin:15% auto;
}
.MyName{
  font-size: 120%;
}
.MyName p{
  font-size:12px;
}
a{
  text-decoration: none;
  z-index: 6;
}
a:hover{
  cursor:pointer;
  transition: opacity 0.3s ease-in-out;
  opacity: 0.5;
}
.ruler{
  width: 99.8vw;
  border: 1px;
  display: inline-block;
  border-style:inset;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: Header-down 1 var(--show-time) ease-out;
  }
  .show-down{
    animation: Header-down 1 var(--show-time) ease-out;
  }
  .show-in{
    animation: Header-in 1 var(--show-time) ease-out;
  }
  .show-left{
    animation: Header-left 1 var(--show-time) ease-out;
  }
}
@media screen and (max-width:700px){
  .App-logo {
    margin-top: 3%;
    margin-left: 10%;
  }
  .navigation{
    margin-top: 3%;
    margin-right: 0.5%;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes Header-down {
  from {
    transform: translateY(-100px);
  }
  to {
    transform: translateY(0px);
  }
}
@keyframes Header-in{
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes Header-left {
  from {
    transform: translateX(400px);
  }
  to {
    transform: translateX(0px);
  }
}
@keyframes Header-right {
  from {
    transform: translateX(-400px);
  }
  to {
    transform: translateX(0px);
  }
}


@keyframes App-logo-flash{
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

.navigation{
  background: rgb(2,0,36);
  background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.7) 80%, rgba(255,255,255,0) 100%); 
  overflow-x:hidden;
  display: grid;
  grid-template-columns: 20% auto;
  position: sticky;
  top: 0;
  z-index: 10;
}

.item {
  text-align: right;
  margin: 5px;
  padding: 2%;
  border: 1px;
  display: inline-block;
  border-style: outset;
  border-radius: 10%;
  text-decoration: none;
}

.navigation-sub{
  margin-top:auto;
  margin-bottom: auto;
  margin-right: 5%;
  margin-left: 0;
  text-align: right;

}

.footer{
  font-size:15px;
  width: 100vw;
  display: block;
  text-align: center;
  position: fixed;
  bottom: 0;
  background: inherit;
  z-index: 10;
  background: rgb(2,0,36);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.7) 80%, rgba(255,255,255,0) 100%); 
}

.footerGrid{
  display: grid;
  grid-template-columns: 0.90fr 0.1fr 0.1fr 0.90fr;
  margin-bottom: 10px;
}

.content{
  overflow-y: auto;
  z-index: 0;
  padding: auto;
  margin-top: auto;
  margin-bottom: 10vh;
  overflow-x: hidden;
}
.background{
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: -1;
}
.footerSvg{
  display:grid;
  fill: white;
  margin: 5%;
}
.footerSvg:hover{
  cursor: pointer;
}